.talk-us-block .svg-icon-wrap {
  border-radius: 50%;
  background-color: #17fcc4;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 30px;
  width: 30px;
}
@media screen and (max-width: 767px) {
  .talk-us-block .svg-icon-wrap {
    height: 50px;
    width: 50px;
  }
}
.talk-us-block .svg-icon-wrap.triangle-element {
  height: 30px;
  width: 30px;
}
.talk-us-block .svg-icon-wrap.triangle-element::before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: #17fcc4 transparent transparent transparent;
}
@media screen and (max-width: 550px) {
  .talk-us-block .svg-icon-wrap.triangle-element::before {
    bottom: -2.6vw;
    right: 3vw;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .talk-us-block .svg-icon-wrap.triangle-element::before {
    bottom: -13px;
    right: 15px;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 1025px) {
  .talk-us-block .svg-icon-wrap.triangle-element::before {
    bottom: -10px;
    right: 5px;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 1921px) {
  .talk-us-block .svg-icon-wrap.triangle-element::before {
    bottom: -13px;
    right: 15px;
    border-width: 17px 16px 0 0;
  }
}
.talk-us-block .contact-icon {
  flex: 0 0 auto;
}
.talk-us-block .contact-icon path,
.talk-us-block .contact-icon circle {
  fill: #17fcc4;
}
.talk-us-block .msg-icon {
  width: 35px;
  height: 35px;
  padding: 5px 10px;
}
.talk-us-block .msg-icon path,
.talk-us-block .msg-icon circle {
  fill: #0f7196;
}
.talk-us-block .msg-icon .st0 {
  opacity: 1;
}
.talk-us-block .email-btn .cls-1-top {
  transition: transform 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.talk-us-block .email-btn:hover .cls-1-top {
  transform: translate(0, 52%) rotateX(130deg);
}
.talk-us-block .email-icon {
  width: 45px;
  height: 45px;
  /*
  &.email-btn &.bottom-email-icon {
  	left: 6px;
  	bottom: 0;
  	transform: none;
  	top: 10px;
  	transform-origin: unset;
  	width: 45px;
  	height: 45px;
  }
  &.top-part-svg {
  	left: 28px;
  	top: 30px;
  }
  */
}
.talk-us-block .phone-icon {
  width: 51px;
  height: 51px;
  padding: 11px 5px 13px 9px;
}
@media screen and (max-width: 767px) {
  .talk-us-block .phone-icon {
    width: 45px;
    height: 45px;
  }
}
.talk-us-block .phone-icon .signal-path {
  opacity: 0;
}
.talk-us-block .msg-icon .st0 {
  fill-rule: evenodd;
  clip-rule: evenodd;
}
.talk-us-block .checkmark-blue {
  height: 35px;
  width: 30px;
}
.talk-us-block a[href^="tel:"]:not(.btn-default):not(.contact-section-btn) {
  color: #fff !important;
}